import React from "react";

const Moontitle = (props) => {
  if (props.title) {
    return <p></p>;
  }
};

const Mooninfo = (props) => {
  if (props.displayStatus) {
    return (
      <div>
        <p></p>
        <p>
          
          2000
        </p>
        <button onClick={props.onClickEvent}></button>
      </div>
    );
  }
};

export { Moontitle, Mooninfo };
