import React from "react";

const Timetitle = (props) => {
  if (props.title) {
    return <p></p>;
  }
};

const Timeinfo = (props) => {
  if (props.displayStatus) {
    return (
      <div>
        <p></p>
        <p>
          
          <br />
          
          ...
        </p>
        <button onClick={props.onClickEvent}></button>
      </div>
    );
  }
};

export { Timetitle, Timeinfo };
